  from{
    opacity: 0;
    transform: translateY(-30px);
  }

  to{
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes editor{
  from{
    opacity: 0;
    transform: translateX(70px);
  }

  to{
    opacity: 1;
    transform: translateX(0);
  }
}

